import React, {
	useMemo,
	useState,
	useCallback,
	useRef
} from 'react'
import { createPortal } from 'react-dom'
import './AddNote.css'

const Field = React.memo(({ title, setTitle, valide }) => {
	const titleHelper = useRef(null)
	const [help, setHelp] = useState("Enter a title without special character .")

	const handleHelper = useCallback(() => {
		const h = titleHelper.current
		let helper = ""

		if (h.classList.contains('text-danger')) h.classList.remove('text-danger')
		if (h.classList.contains('text-success')) h.classList.remove('text-success')
		if (h.classList.contains('text-warning')) h.classList.remove('text-warning')

		if ((title.length === 0 || (/[///#%&*<>]+/.test(title)))) {
			if (title.length === 0) {
				h.classList.add('text-danger')
				helper = "Enter something."
				valide(false)
			} else if ((/[///#%&*<>]+/.test(title))) {
				h.classList.add('text-warning')
				helper = "Enter a title without special character ."
				valide(false)
			} else {
				h.classList.add('text-success')
				helper = "Good!"
				valide(true)
			}
		} else {
			h.classList.add('text-success')
			helper = "Good!"
			valide(true)
		}
		setHelp(helper)
	}, [title, valide])

	const handleTitle = useCallback((e) => {
		setTitle(e.target.value)
	}, [setTitle])

	return (<div className="form-group">
		<label htmlFor="titre">titre</label>
		<input type="text" autoComplete="off" className="form-control" id="titre" aria-describedby="ChangeTitre" value={title} onChange={handleTitle} onKeyUp={handleHelper}/>
		<small id="ChangeTitre" ref={titleHelper} className="form-text">{help}</small>
	</div>)
})

const Text = React.memo(({ description, setDescription, valide }) => {
	const descrHelper = useRef(null)
	const [help, setHelp] = useState("Enter your description")

	const handleHelper = useCallback(() => {
		const d = descrHelper.current
		let helper = ""

		if (d.classList.contains('text-danger')) d.classList.remove('text-danger')
		if (d.classList.contains('text-success')) d.classList.remove('text-success')
		if (d.classList.contains('text-warning')) d.classList.remove('text-warning')

		if (description.length === 0) {
			if (description.length === 0) {
				d.classList.add('text-danger')
				helper = "Enter your description or an space"
				valide(false)
			} else {
				d.classList.add('text-success')
				helper = "Good!"
				valide(true)
			}
		} else {
			d.classList.add('text-success')
			helper = "Good!"
			valide(true)
		}
		setHelp(helper)
	}, [description, valide])

	const handleDescription = useCallback((e) => {
		setDescription(e.target.value)
	}, [setDescription])

	return (<div className="form-group">
		<label htmlFor="description">description</label>
		<textarea className="form-control" id="description" value={description} aria-describedby="ChangeDescription" onChange={handleDescription} onKeyUp={handleHelper}></textarea>
		<small id="ChangeDescription" ref={descrHelper} className="form-text">{help}</small>
	</div>)
})

export const AddNote = React.memo(({ show, close, submit, id }) => {
	const [title, setTitle] = useState('')
	const [description, setDescription] = useState('')
	const [titleValide, settitleValide] = useState(false)
	const [descriptionValide, setdescriptionValide] = useState(false)

	const handletitleValide = useCallback((bool) => {
		settitleValide(bool)
	}, [])

	const handledescriptionValide = useCallback((bool) => {
		setdescriptionValide(bool)
	}, [])

	const handleSubmit = useCallback((e) => {
		e.preventDefault()
		if (titleValide && descriptionValide) {
			setTitle('')
			setDescription('')
			submit({ type: 'add', note: { id: id, t: title, d: description } })
			close()
		}
	}, [title, description, submit, close, titleValide, descriptionValide, id])

	const animationStyle = useMemo(() => {
		return show ? { transform: 'translateY(0)' } : { transform: 'translateY(-150%)' }
	}, [show])

	return createPortal(<div className="add-note shadow" style={animationStyle}>
		<form>
			<Field
				title={title}
				setTitle={setTitle}
				valide={handletitleValide}
			/>
			<Text
				description={description}
				setDescription={setDescription}
				valide={handledescriptionValide}
			/>
			<button type="submit" onClick={handleSubmit} className="btn btn-primary">add</button>
		</form>
	</div>, document.body)
})