import React from 'react'
import 'css.gg'
import './IButtons.css'

const Button = React.memo(({type,onClick,bubble}) => {
	return (<div className="btn-i w-sm-100 bg-info" title={bubble}>
				<button className="bg-dark" onClick={onClick}>
					<i className={type}></i>
				</button>
			</div>)
})

export const IButtons = ({search,add,remove}) => {
	return (<div className="i-btn d-flex flex-row flex-sm-column">
		<Button 
			type="gg-search" 
			onClick={search}
			bubble="search note"
		/>
		<Button 
			type="gg-add-r" 
			onClick={add}
			bubble="add note"
		/>
		<Button 
			type="gg-trash" 
			onClick={remove}
			bubble="clear note"
		/>
	</div>)
}