import React, {
	useState,
	useCallback,
	useEffect,
	useRef
} from 'react'
import { createPortal } from 'react-dom'
import './NoteView.css'
import './Note.css'
import 'css.gg/icons/css/trash.css'


const Alert = React.forwardRef(({ id, title, description, close, update }, ref) => {
	const [newt, sett] = useState(title)
	const [newd, setd] = useState(description)
	const [up, setu] = useState(false)
	const btn = useRef(null)
	const desc = useRef(null)

	useEffect(() => {
		if (!up) desc.current.innerHTML = newd
	}, [description, newd, up])

	const handleT = useCallback((e) => {
		sett(e.target.value)
		btn.current.classList.value = "btn btn-primary"
	}, [])

	const handleD = useCallback((e) => {
		setd(e.target.value)
		btn.current.classList.value = "btn btn-primary"
	}, [])

	const handleUpdate = useCallback((e) => {
		if (up) {
			if (newd.length > 0 && newt.length > 0 && !(/[///#%&*<>]+/.test(newt))) {
				e.preventDefault()
				update({ type: 'update', note: { id: id, t: newt, d: newd } })
				close()
				setu(false)
			} else {

				// animation when modification invalid
				const b = btn.current
				b.classList.value = "btn"
				b.classList.add('shock')
				b.classList.add('btn-danger')
				window.setTimeout(() => { b.classList.value = "btn btn-primary" }, 2000)
			}
		} else {
			setu(true)
			desc.current.innerHTML = newd === '' ? description : newd;
		}
	}, [newt, newd, update, close, id, up, description])

	const handleClose = useCallback(() => {
		sett(title)
		setd(description)
		close()
	}, [close, title, description])

	return createPortal(<div ref={ref}>
		<div className="alert shadow alert-dim">
			{
				up ?
					<>
						<input value={newt} onChange={handleT} id="ntitle" name="ntitle" />
						<textarea name="ndescription" id="ndescription" ref={desc} value={newd} onChange={handleD}>{description}</textarea>
					</> :
					<>
						<h2 className="tf">{title}</h2>
						<div className="df" ref={desc}></div>
					</>
			}
			<div className="close" onClick={handleClose}>&times;</div>
			<div className="modify">
				<button type="submit" className="btn btn-primary" ref={btn} onClick={handleUpdate}>{up ? 'save' : 'modify'}</button>
			</div>
		</div>
		<div className="fond-alert" onClick={handleClose}></div>
	</div>, document.body)
})

const Note = React.memo(({ id, title, description, update, remove }) => {
	const n = useRef(null)
	const note = useRef(null)
	const desc = useRef(null)

	useEffect(() => {
		desc.current.innerHTML = description === ' ' ? '{space}' : description
	}, [description])

	const togglestyle = useCallback(() => {
		const cl = n.current.classList
		cl.toggle('window-mode')
	}, [])

	const handleDelete = useCallback(() => {
		// animation de suppression
		note.current.style.opacity = "0"
		window.setTimeout(() => {
			remove({ type: 'delete', note: { id: id } })
		}, 1000)
	}, [id, remove])

	return (<>
		<div className="note note-flex position-relative" ref={note}>
			<div onClick={togglestyle} className="card bg-light ">
				<div className="card-body">
					<h5 className="card-title text-truncate">{title}</h5>
					<hr />
					<p className="card-text note-text text-truncate" ref={desc}></p>
				</div>
			</div>
			<div className="delete">
				<button onClick={handleDelete} className="btn btn-absolute btn-circle btn-danger d-flex justify-content-center align-items-center">
					<i className="gg-trash"></i>
				</button>
			</div>
		</div>
		<Alert
			ref={n}
			id={id}
			title={title}
			description={description}
			close={togglestyle}
			update={update}
		/>
	</>)
})

export const NoteView = React.memo(({ filter, data, update, remove }) => {

	const [Notes, setNotes] = useState(null)

	const handleNotes = useCallback(() => {
		const notes = []
		data.forEach((note, key) => {
			if (filter === '' || note.t.includes(filter))
				notes.push(<Note
					key={note.id}
					id={note.id}
					title={note.t}
					description={note.d}
					update={update}
					remove={remove}
				/>)
		})
		setNotes(notes)
	}, [data, update, remove, filter])

	useEffect(() => {
		handleNotes()
	}, [data, handleNotes])

	return (<div className="note-view">
		{Notes}
	</div>)
})
