import React, { useMemo } from 'react'
import { createPortal } from 'react-dom'
import './SearchBar.css'
import './../../node_modules/css.gg/icons/css/search.css'

export const SearchBar = React.memo(({filter,changefilter,show}) => {

	const handleChange = (e) => {
		changefilter(e.target.value)
	}
	
	const animationStyle = useMemo(() => {
		return show ? {transform: 'translateY(0)'} : {transform: 'translateY(-150%)'}
	},[show])

	return createPortal(<div className="search-bar d-flex shadow" style={animationStyle}>
		<input type="text" className="form-control" autoComplete="off" id="search" name="search" value={filter} onChange={handleChange}/>
		<button className="btn-search"><i className="gg-search"></i></button>
	</div>,document.body)
})

