import React, {
	useCallback, useEffect, useRef
} from 'react'
import './Popup.css'
import { createPortal } from 'react-dom'

export const Popup = React.memo(({clear,show,close}) => {
	const p = useRef(null)

	useEffect(() => {
		if (show)
			p.current.classList.add('show-mode')
	}, [show])
	
	const handleClose = useCallback(() => {
		close()
		p.current.classList.remove('show-mode')
	},[close])

	const handleClear = useCallback(() => {
		clear({type: 'clear'})
		handleClose()
	},[clear,handleClose])

	return createPortal(<div ref={p}>
		<div className="fond" onClick={handleClose}></div>
		<div className="popup-content">
			<div className="popup-description">do you want to erase everything ?</div>
			<div className="popup-action">
				<button className="btn btn-success" onClick={handleClear}>confirm</button>
				<button className="btn btn-danger" onClick={handleClose}>cancel</button>
			</div>
		</div>
	</div>,document.body)
})