import React, {
	useState,
	useReducer,
	useCallback,
	useEffect
} from 'react';
import './App.css'
import { get , set } from './localStorage'
import { SearchBar } from './components/SearchBar'
import { AddNote } from './components/AddNote'
import { NoteView } from './components/NoteView'
import { IButtons } from './components/IButtons'
import { Popup } from './components/Popup'
import { AIDE } from './help'
import { README } from './readme'

const HELP = {
	id: 0,
	t: 'Hi guys!',
	d: AIDE
}

const READMEH = {
	id: 1,
	t: 'README',
	d: README
}

const reducer = (state, action) => {
	let nstate = null
	switch (action.type) {
		case 'add':
			nstate = [...state,action.note]
			set(nstate)
			return nstate
		case 'delete':
			nstate =  state.filter((e) => {
				return (e.id !== action.note.id)
			})
			set(nstate)
			return nstate
		case 'clear':
			set([])
			return []
		case 'update':
			nstate =  state.map((e) => {
				if (e.id === action.note.id)
					return action.note
				else
					return e
			})
			set(nstate)
			return nstate
		default:
			return state
	}
}

const init = (arg) => {
	const data = get()
	return data !== null && data.length > 0 ? data : arg
}

const useToggle = (param) => {
	const [obj, setobj] = useState(param)
	const toggle = () => {
		setobj(s => !s)
	}
	return [obj, toggle]
}

const App = () => {
	const [searchMode, showsearchbar] = useToggle(false)
	const [addMode, showaddbar] = useToggle(false)
	const [removeMode, doremove] = useToggle(false)
	const [search, setsearch] = useState('')
	const [data, dispatch] = useReducer(reducer, [HELP,READMEH], init)

	const resetMode = useCallback(() => {
		if (searchMode) showsearchbar()
		if (addMode) showaddbar()
		if (removeMode) doremove()
	}, [showsearchbar, showaddbar, doremove, searchMode, addMode, removeMode])

	const searchbar = useCallback(() => {
		if (!searchMode) resetMode()
		showsearchbar()
	}, [resetMode, showsearchbar, searchMode])
	const addbar = useCallback(() => {
		if (!addMode) resetMode()
		showaddbar()
	}, [resetMode, showaddbar, addMode])
	const remove = useCallback(() => {
		if (!removeMode) resetMode()
		doremove()
	}, [resetMode, doremove, removeMode])

	useEffect(() => {
		if (localStorage) {

		}
	},[])

	return (<div className="d-flex flex-column-reverse flex-sm-row parent">
		<div className="menu bg-dark">
			<IButtons
				search={searchbar}
				add={addbar}
				remove={remove}
			/>
		</div>
		<div className="content blue">
			<NoteView
				filter={search}
				data={data}
				update={dispatch}
				remove={dispatch}
			/>
		</div>
		<SearchBar
			filter={search}
			changefilter={setsearch}
			show={searchMode}
		/>
		<AddNote
			submit={dispatch}
			show={addMode}
			close={showaddbar}
			id={data.length}
		/>
		<Popup
			clear={dispatch}
			show={removeMode}
			close={doremove}
		/>
	</div>);
}

export default App;